.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.item {
    background: #2196F3;
    color: #fff;
    font-weight: bold;
    text-align: center;
    margin-bottom: 1px;
    padding: 10px;
}

.btns {
  margin-top: 20px;
}

.btn-add, 
.btn-remove {
  background: #9E9E9E;
  color: #FFf;
  text-align: center;
  padding: 10px;
}
.btn-remove {
    background: #F44336;
}

.anim_index {
    border-bottom: 1px solid #d4d4d4;
    margin-bottom: 10px;
    background: #e0e0e0;
}
.anim_index a {
    background: #E91E63;
    display: inline-block;
    margin: 5px;
    padding: 5px 10px;
    border-radius: 5px;
    color: white;
    text-decoration: none;
    font-size: 12px;
}
.showDiv {
  background: #03A9F4;
  color: white;
  padding: 10px 20px;
  display: inline-block;
  cursor: pointer;
}

/*=======================================
            TRANSITIONS BASIC 
========================================*/

.square {
  background: red;
  height:100px;
  opacity:1;
  -webkit-transition: all 2s ease;
  -o-transition: all 2s ease;
  transition: all 2s ease;
}

.square.square-entering {
  opacity: 1;
  -webkit-transform: translateX(0%);
      -ms-transform: translateX(0%);
          transform: translateX(0%);
}

.square.square-entered {
  background: blue;
}

.square.square-exiting {
  background: green
}

.square.square-exited {
  opacity: 0;
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%)
}


/*=======================================
                CSSTRANSITIONS
========================================*/

.square.false {
  background: #feaaa3;
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
}

.square.true {
  -webkit-transform: translateX(0%);
      -ms-transform: translateX(0%);
          transform: translateX(0%);
}

.square-enter.square-enter-active {
  background: blue;
}

.square-exit.square-exit-active {
  background: green;
}


/*=======================================
            TRANSITIONS GROUP 
========================================*/

.item {
  opacity: 0;
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: all .5s ease-in;
  -o-transition: all .5s ease-in;
  transition: all .5s ease-in;
}

.item.active,
.item-enter.item-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0%);
      -ms-transform: translateX(0%);
          transform: translateX(0%);
}

.item-exit.item-exit-active {
  opacity: 0;
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%)
}


